<template>
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="电价" name="powerPrice"></el-tab-pane>
        <el-tab-pane label="燃气价格" name="gasPrice"></el-tab-pane>
        <el-tab-pane label="热/冷价格" name="heatPrice"></el-tab-pane>
      </el-tabs>
      <div>
        <powerPrice v-if="showpowerPrice"></powerPrice>
        <gasPrice v-if="showgasPrice"></gasPrice>
        <heatPrice v-if="showheatPrice"></heatPrice>
      </div>
    </div>
</template>

<script>
  import powerPrice from './powerPrice.vue';
  import gasPrice from './gasPrice.vue';
  import heatPrice from './heatPrice.vue';
    export default {
        data () {
            return {
              activeName:'powerPrice',
              showpowerPrice:true,
              showgasPrice:false,
              showheatPrice:false
            }
        },
      methods: {
        handleClick(tab, event) {
          var that = this;
          if(tab.paneName == 'powerPrice'){
            that.showpowerPrice = true;
            that.showgasPrice = false;
            that.showheatPrice = false;
          }else if(tab.paneName == 'gasPrice'){
            that.showpowerPrice = false;
            that.showgasPrice = true;
            that.showheatPrice = false;
          }else if(tab.paneName == 'heatPrice'){
            that.showpowerPrice = false;
            that.showgasPrice = false;
            that.showheatPrice = true;
          }
        },


      },
      components:{
        powerPrice,gasPrice,heatPrice
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



